<template>
	<view class="container" v-if="isshow">
		<watermark></watermark>
		<view :class="type == 4?'project_top project_box':'project_select project_box'" v-if="buildList.length > 0 || unitList.length > 0">
			<u-dropdown :close-on-click-mask="true" active-color="#03958C">
				<u-dropdown-item v-model="value1" :title="name1" :height="420" :options="buildList" @change="_getUnit()"></u-dropdown-item>
				<u-dropdown-item v-model="value2" :title="name2" :height="420" :options="unitList" @change="_getList()"></u-dropdown-item>
			</u-dropdown>
		</view>
		<room :status="status" v-if="list.length > 0" :houseList="list"></room>
		<template v-else>
			<image class="noData" src="@/static/img/noData.png" mode=""></image>
		</template>
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import room from '@/pages/salemanagerVisual/house/components/room.vue';
	import tabBar from '@/components/tabBar3.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		props: ['type', 'status'],
		data() {
			return {
				isshow: false, // 是否显示页面
				value1: '', // 楼栋
				value2: '', // 单元
				name1: '', // 楼栋显示
				name2: '', // 单元显示
				postData: { // 请求参数
					buildId: '',
					unitNo: ''
				},
				buildList: [], // 楼栋list
				unitList: [], // 单元list
				list: [], // 房源list
			}
		},
		mounted() {
			this._getBuild()
		},
		methods: {
			// 获取楼栋
			_getBuild() {
				let that = this
				httpUtils.sendHttpRequest('2004', Object.assign({}, ''), function(res) {
					if (res.data.code == '0') {
						that.isshow = true
						that.buildList = res.data.data
						that.value1 = that.value1 ? that.value1 : (that.buildList.length > 0 ? that.buildList[0].value : '')
						if (that.value1) {
							that._getUnit()
						}
					}
				})
			},
			// 获取单元
			_getUnit() {
				let that = this,
					postData = {
						buildId: that.value1
					}
				httpUtils.sendHttpRequest('2005', Object.assign({}, postData), function(res) {
					if (res.data.code == '0') {
						that.unitList = res.data.data
						that.value2 = that.value2 ? that.value2 : (that.unitList.length > 0 ? that.unitList[0].value : '')
						if (that.value2) {
							that._getList()
						}
						// 动态获取楼栋title值
						that.buildList.forEach((item, index) => {
							if (item.value == that.value1) {
								that.name1 = item.label
							}
						});
					}
				})
			},
			// 获取列表
			_getList() {
				let that = this,
						questCode = this.type == 4 ? '4002' : '2003'
				that.postData.buildId = that.value1,
				that.postData.unitNo = that.value2,
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest(questCode, Object.assign({}, that.postData), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.list = res.data.data
						// 动态获取单元title值
						that.unitList.forEach((item, index) => {
							if (item.value == that.value2) {
								that.name2 = item.label
							}
						});
					}
				})
			},
		},
		components: {
			watermark,
			room,
			tabBar
		}
	}
</script>

<style>
	page {
		height: 100%;
	}
	.project_top{
		top: 136rpx!important;
	}
	/* #ifdef H5 */
	.project_top{
		top: 44px!important;
	}
	.project_select{
		top: 44px!important;
	}
	/* #endif */
	.project_box {
		background-color: #fff;
		position: fixed;
		top: 0;
		width: 100%;
		background: #fff;
		z-index: 2;
		border-bottom: 1rpx solid #f6f6f6;
	}
</style>
